body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ui .qBox .segment {
  margin:0 25% 10px 25%;
  height: 35px;
}
.titleWrap {
  text-align: center;
}
.qTitle {
margin-right: 10px;
}

.parent {
  position: absolute;
}
.qCont {
  margin:0 25% 10px 25%;
}

.toggleComp {
  margin: 0 10px 0 10px;
}

.radioBtn {
  margin-right: 10px;
}

.radioBtnCont {
  margin-top: 10px;
}
.summ {
  margin-top: 50px;
}
.logInForm {
  height: 100vh;
}
.welcomeMsg {
  text-align: center;
  width: 100%;
  font-size: 40px;
}

.homePage .background {
  background-image: url("https://images.pexels.com/photos/48604/pexels-photo-48604.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  height: 100vh;
  background-size: cover;
}

.homePage .title {
  margin-top: 5vh;
  float: right;
  font-size: 50px;
  text-align: right;
  max-width: 50%;
  font-family: 'Raleway', sans-serif;
}
.homePage .smallTitle {
  font-size: 20px;
}

